.btn-cta {
  background: var(--orange);
  color: #fff;
  text-decoration: none;
  border-radius: 0.625rem;
  font-size: 1.125rem;
  padding: 0.5rem 0.75rem;
  display: inline-block;
}
.btn-cta:active {
  transform: scale(0.98);
}
.btn-cta:hover {
  background: var(--darkOrange);
}

.title, .sub-title {
  margin: 0;
  font-weight: 600;
  color: var(--orange);
}

.btn-link {
  text-decoration: none;
  color: var(--orange);
  font-size: 0.875rem;
}
.btn-link:hover {
  text-decoration: underline;
}

ul {
  -webkit-padding-start: 0;
}

.swiper-pagination-bullet-active {
  background: var(--orange);
}

.swiper-pagination-bullet {
  width: 0.75rem;
  height: 0.75rem;
}

@media (min-width: 768px) {
  p {
    font-size: 1.0625rem;
  }

  .btn-cta {
    font-size: 1.125rem;
    padding: 0.5rem 0.75rem;
    border-radius: 0.625rem;
  }
}
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1001;
  background: var(--orange);
  display: flex;
  justify-content: center;
}
.preloader .preloader-logo {
  width: 80%;
  max-width: 31.25rem;
}
.preloader .preloader-logo path,
.preloader .preloader-logo rect {
  stroke-linecap: square;
  fill: rgba(255, 255, 255, 0);
}

.main-header {
  width: 100%;
  backdrop-filter: saturate(180%) blur(20px);
  background: rgba(255, 255, 255, 0.72);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
}
.main-header:after {
  content: "";
  background: rgba(0, 0, 0, 0.16);
  height: 0.0625rem;
  width: 100%;
  position: absolute;
  top: 100%;
  left: 0;
}
.main-header .container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 0;
}
.main-header .logo {
  width: 7.5rem;
  display: block;
}
.main-header .logo img {
  display: block;
  width: 100%;
}
.main-header .nav-toggle {
  background: transparent;
  display: flex;
  width: 2rem;
  border: 0;
  padding: 0;
  cursor: pointer;
}
.main-header .nav-toggle .toggle-lines {
  stroke-linecap: square;
  height: 3rem;
}
.main-header .nav-toggle .toggle-lines rect {
  fill: var(--orange);
}
.main-header .main-nav {
  flex: 100% 1;
  align-items: center;
  overflow: hidden;
}
.main-header .main-nav .nav {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  margin: 0;
}
.main-header .main-nav .nav-link {
  display: block;
  text-decoration: none;
  color: var(--gray);
  text-align: center;
  padding: 0.5rem 0;
}
.main-header .main-nav .nav-link:hover {
  color: var(--orange);
}

@media (min-width: 768px) {
  .main-header .logo {
    width: 9.375rem;
  }
}
@media (min-width: 992px) {
  .main-header .container {
    flex-flow: row wrap;
    justify-content: flex-start;
  }
  .main-header .nav-toggle {
    display: none;
  }
  .main-header .main-nav {
    display: flex;
    flex-basis: auto;
    height: auto !important;
  }
  .main-header .main-nav .nav {
    flex-direction: row;
    margin-left: auto;
    gap: 1.875rem;
  }
}
.hero {
  height: 100vh;
  padding-top: 4rem;
}
.hero .hero-wrapper {
  position: relative;
  justify-content: center;
  height: 100%;
  display: flex;
  align-items: center;
  overflow: hidden;
}
.hero .hero-img {
  background: url("/dist/images/hero.jpg") no-repeat center center;
  background-size: cover;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.hero .hero-text {
  text-align: center;
}
.hero .hero-text h1 {
  color: var(--white);
  font-size: 8vw;
  line-height: 8vw;
  margin-bottom: 3.3333333333vw;
}
.hero .hero-text p {
  color: var(--white);
  max-width: 78.6666666667vw;
  width: 90%;
  margin: 0 auto 3.3333333333vw;
}

@media (min-width: 768px) {
  .hero .hero-text h1 {
    font-size: 3.75rem;
    margin-bottom: 1.5625rem;
    line-height: 3.75rem;
  }
  .hero .hero-text p {
    font-size: 1.0625rem;
    line-height: 1.625rem;
    margin-bottom: 1.5625rem;
    max-width: 36.875rem;
  }
}
.platforms {
  text-align: center;
  padding: 6.6666666667vw 0;
}
.platforms .container {
  display: grid;
  gap: 4vw;
}
.platforms .title {
  margin-bottom: 6.6666666667vw;
}
.platforms .sub-title {
  margin-bottom: 2vw;
}
.platforms img {
  margin: 0 auto 4vw;
}
.platforms p {
  margin-bottom: 2vw;
}

@media (min-width: 768px) {
  .platforms {
    padding: 3.125rem 0;
  }
  .platforms .container {
    grid-template-columns: 1fr 1fr;
    justify-content: space-between;
  }
  .platforms .title {
    margin-bottom: 3.125rem;
    font-size: 2.25rem;
  }
  .platforms .sub-title {
    margin-bottom: 0.9375rem;
  }
  .platforms p {
    text-align: left;
    margin-bottom: 0.9375rem;
  }
  .platforms img {
    margin: 0 auto 1.875rem;
  }
}
.secure {
  text-align: center;
  background: var(--lightGray);
  padding: 3.125rem 0;
}
.secure .secure-text {
  display: grid;
  gap: 1.875rem;
}
.secure .title {
  margin-bottom: 0.9375rem;
}
.secure .title + p {
  margin-bottom: 1.875rem;
}
.secure .sub-title {
  margin-bottom: 2vw;
}
.secure .secure-icons {
  display: block;
  margin: 0 auto;
  width: 6.25rem;
}

@media (min-width: 768px) {
  .secure {
    padding: 3.125rem 0;
  }
  .secure .secure-text {
    grid-template-columns: 1fr 1fr 1fr;
    gap: 3.125rem;
    justify-content: space-between;
  }
  .secure .title {
    font-size: 2.25rem;
    margin-bottom: 0.9375rem;
  }
  .secure .title + p {
    max-width: 50rem;
    margin: 0 auto 3.125rem;
  }
  .secure .sub-title {
    margin-bottom: 0.9375rem;
  }
  .secure p {
    margin-bottom: 0.9375rem;
  }
}
.stories {
  text-align: center;
}
.stories .swiper-slide {
  padding: 0 5%;
  box-sizing: border-box;
}
.stories .lead {
  font-size: 1rem;
  font-weight: 900;
}
.stories .img-circle {
  border-radius: 50%;
  display: block;
  margin: 0 auto;
}

.main-footer {
  background: var(--orange);
  color: var(--white);
  padding: 0.9375rem 0;
}
.main-footer .container {
  display: grid;
  grid-template-columns: 1fr;
}
.main-footer .footer-logo {
  width: 7.5rem;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0.9375rem;
}
.main-footer .left ul {
  list-style-type: none;
  margin: 0;
  display: flex;
  justify-content: center;
  gap: 4vw;
}
.main-footer .left a {
  color: #fff;
}
.main-footer .right {
  text-align: center;
}
.main-footer .right .social-icons {
  height: 2.6875rem;
  display: flex;
  align-items: center;
  gap: 4vw;
  justify-content: center;
}

@media (min-width: 768px) {
  .main-footer .container {
    grid-template-columns: 1fr 1fr;
  }
  .main-footer .footer-logo {
    width: 9.375rem;
    margin-left: 0;
    margin-right: auto;
    margin-bottom: 0.9375rem;
  }
  .main-footer .left ul {
    gap: 1.875rem;
    justify-content: left;
  }
  .main-footer .right {
    text-align: right;
  }
  .main-footer .right .social-icons {
    gap: 1rem;
    justify-content: flex-end;
    margin-bottom: 0.9375rem;
  }
}